@import "reset";
@import "fonts";
@import "vars";
.wrapper {
    display: flex;
    min-height: 100vh;
    flex-direction: column;
    width: 100%;
    overflow-x: hidden;
    margin: 0 auto;
    font-family: $font-primary;
}

// COMMON BUTTONS AND INPUTS
th,
td {
    box-sizing: border-box;
    vertical-align: middle;
}

input,
select,
button {
    font-family: $font-primary;
    box-sizing: border-box;
    &:focus {
        outline: none;
    }
}

select {
    -webkit-appearance: none;
    -moz-appearance: none;
    background-image: url("data:img/png;base64,");
    background-position: calc(100% - 5px);
    background-size: 10px 6px;
    background-repeat: no-repeat;
    &:hover {
        cursor: pointer;
    }
}

button,
input[type=checkbox] {
    cursor: pointer;
    box-sizing: border-box;
    padding: 0;
}

button a {
    display: inline-block;
    line-height: 28px;
    color: #fff;
}

div[role="button"] a {
    display: inline-block;
    line-height: 32px;
    height: 32px;
    color: #fff;
}

.btn {
    height: 28px;
    line-height: 28px;
    font-size: 14px;
    color: #fff;
    font-weight: 700;
    border: none;
    border-radius: 3px;
    cursor: pointer;
}

.btn--ugly-blue {
    font-size: 14px;
    background-color: $ugly-blue;
    border: solid 1px #dfdfdf;
    &:hover {
        background-color: darken($ugly-blue, 10%)
    }
    a {
        padding-left: 20px;
        padding-right: 20px;
    }
}

.tablenav__button--add {
    position: relative;
    color: #fff;
    background-image: linear-gradient(to top, #2a5272, $ugly-blue);
    border: solid 1px #2a5272;
    i {
        position: absolute;
        top: 9px;
        left: 18px;
    }
    a {
        padding-left: 34px;
        padding-right: 19px;
        font-size: 16px;
    }
}

.btn--light-grey {
    background-color: #9c9c9c;
    &:hover {
        background-color: darken(#9c9c9c, 10%)
    }
}

.btn--comm {
    display: inline-block;
    width: 43px;
    margin-right: 8px;
    line-height: 28px;
    text-align: center;
    color: #444;
    background-image: linear-gradient(to top, #e3e3e3, #f6f6f6);
    border: solid 1px #dfdfdf;
    &:hover {
        background-image: none;
        background-color: lighten(#f6f6f6, 2%);
    }
}

.btn--water-blue {
    background-color: #1e84db;
    &:hover {
        background-color: darken(#1e84db, 10%);
    }
}

.btn--square {
    width: 106px;
    height: 32px;
    margin-right: 8px;
    font-size: 16px;
    color: #fff;
    font-family: "Myriad Pro", Arial;
    border: none;
    transition: background-color .2s ease;
    cursor: pointer;
    outline: none;
}

.btn--blue {
    background-color: #1e84db;
    &:hover {
        background-color: darken(#1e84db, 10%);
    }
}

.btn--grey {
    background-color: #a0a0a0;
    &:hover {
        background-color: darken(#a0a0a0, 10%);
    }
}

// checkbox input with pseudo-check
.input-box {
    position: relative;
    height: 32px;
    width: 100%;
    padding-left: 30px;
    padding-right: 15px;
    cursor: pointer;
}

.input--check {
    position: absolute;
    left: 8px;
    top: 2px;
    visibility: hidden;
    cursor: pointer;
    &:checked+span>i {
        visibility: visible;
    }
}

.input--checkpseudo {
    position: absolute;
    left: 8px;
    top: 2px;
    width: 14px;
    height: 14px;
    background-color: #ffffff;
    border: solid 1px #b3b3b3;
    i {
        visibility: hidden;
        position: absolute;
        left: 1px;
        top: 0px;
        font-size: 14px;
    }
}

// end of checkbox button 
// END COMMON BUTTONS AND INPUTS
.container {
    max-width: 1170px;
    margin: 0 auto;
    padding-left: 1%;
    padding-right: 1%;
}

// HEADER
.header {
    color: #fff;
    .container {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        // justify-content: space-between;
    }
    width: 100%;
    background-color: $ugly-blue;
    box-shadow: 0px 3px 7px 0 rgba(1,
    1,
    1,
    0.25);
    // logotype
    &__logo-box {
        margin-right: 35px;
        padding: 21px 6px 17px 11px;
        background-color: #fff;
        box-shadow: 0px 3px 7px 0 rgba(1, 1, 1, 0.25);
    }
    &__logo {
        width: 201px;
        height: 37px;
    }
    // search
    $search-height: 50px;
    .header__search-box {
        display: flex;
        max-width: 534px;
        min-width: 220px;
        height: $search-height;
        line-height: $search-height;
        background-color: rgba(0, 0, 0, .2);
        background-color: rgba(0, 0, 0, .2);
        &:hover {
            background-color: rgba(0, 0, 0, .3);
        }
        input,
        button {
            height: $search-height;
            line-height: $search-height;
            color: #fff;
            background-color: inherit;
            border: none;
        }
    }
    .header__search-field {
        width: 28vw;
        height: $search-height;
        line-height: $search-height;
        padding-left: 2.25vw;
        font-size: 16px;
        font-weight: 300;
        &::-webkit-input-placeholder {
            color: #f6f4f4!important;
        }
        &::-moz-placeholder {
            color: #f6f4f4!important;
        }
        &:-ms-input-placeholder {
            color: #333!important;
        }
        &::placeholder {
            color: #f6f4f4!important;
        }
    }
    .header__search-button {
        padding-right: 27px;
        font-size: 24px;
    }
    // header navbar
    .header__navbar {
        display: flex;
        align-items: center;
        margin-left: auto;
        align-self: center;
    }
    .header__settings {
        margin-right: 31px;
        .settings__icon {
            display: inline-block;
            width: 28px;
            height: 28px;
            background-image: url("data:img/png;base64,");
        }
    }
    .header__notifications {
        position: relative;
        margin-right: 20px;
        font-size: 26px;
    }
    .notifications__number {
        position: absolute;
        top: -2px;
        right: -5px;
        width: 18px;
        height: 18px;
        line-height: 18px;
        font-size: 11.5px;
        text-align: center;
        background-color: #d22423;
        border-radius: 50%;
        &:hover {
            cursor: pointer;
        }
    }
    .header__account {
        display: flex;
        align-items: center;
        font-size: 16px;
        color: #fff;
        text-transform: none;
        &:hover {
            cursor: pointer;
        }
        &:hover .account__name {
            color: #e3e3e3;
            transition: color 0.05s linear;
        }
        .account__avatar {
            width: 40.8px;
            height: 41px;
            margin-right: 12px;
            overflow: hidden;
        }
        .account__name {
            display: inline-block;
            margin-right: 12px;
        }
    }
    i:hover {
        cursor: pointer;
    }
}

// responsive menu
@media only screen and (max-width: 800px) {
    .header .container {
        justify-content: center;
        max-width: 535px;
        padding-top: 10px;
        .header__search-box {
            order: 3;
            margin-top: 30px;
            margin-bottom: 30px;
            .header__search-field {
                width: 80vw;
            }
        }
        .header__navbar {
            order: 2;
            margin-left: 0;
        }
    }
}

@media only screen and (max-width: 535px) {
    .header .container {
        .header__logo-box {
            margin-right: 0;
        }
        .header__navbar {
            order: 2;
            margin-top: 30px;
            margin-left: 0;
        }
    }
}

// end responsive menu
@media screen and (-ms-high-contrast: active),
(-ms-high-contrast: none) {
    .header .header__search-box .header__search-field {
        color: #000;
    }
    .wrapper {
        display: block; // avoiding of the floated footer
    }
}

// NAViIGATION
.nav {
    width: 100%;
    margin-bottom: 2px;
    box-shadow: 0px 2px 3px 0 rgba(1, 1, 1, 0.25);
    &__items {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        .active {
            box-shadow: 0px 3px 0px 0px $ugly-blue;
            a {
                color: $ugly-blue;
            }
        }
    }
    &__item {
        margin-right: 1px;
    }
    &__item-link {
        display: inline-block;
        padding: 22px 25px;
        color: $text-dark;
        font-family: $font-primary;
        font-size: 16px;
        &:hover {
            color: $ugly-blue;
            box-shadow: 0px 3px 0px 0px $ugly-blue;
        }
    }
}

@media only screen and (max-width: 968px) {
    .nav {
        display: flex;
        justify-content: center;
        padding: 20px 0px;
        &__items {
            width: 90vw;
            flex-direction: column;
            max-height: 195px;
        }
        &__item-link {
            padding: 12px 15px;
            font-weight: bold;
            text-transform: uppercase;
        }
    }
}

// MAIN
.main {
    flex: 1; // this makes footer sticky to bottom
    background-color: #f6f4f4;
    .container {
        max-width: 1600px;
    }
    // title
    .main__title-box {
        border-bottom: 2px solid #dfdfdf;
    }
    .main__title {
        padding-top: 35px;
        padding-bottom: 15px;
        font-size: 22px;
        font-weight: bold;
        color: $ugly-blue;
    }
    // filter
    .main__filter {
        padding-top: 20px;
        padding-bottom: 60px;
    }
    .tablenav__actions {
        display: flex;
        flex-wrap: wrap;
    }
    .tablenav__button--light {
        height: 32px;
        line-height: 32px;
        padding-left: 15px;
        padding-right: 15px;
        margin-right: 10px;
        font-size: 16px;
        color: #444444;
        background-image: linear-gradient(to right, #e3e3e3, #f6f6f6);
        border: solid 1px #dfdfdf;
        &:hover {
            cursor: pointer;
        }
        i {
            margin-right: 1px;
        }
    }
    .tablenav__button--checkall {
        padding-left: 0;
    }
    // sort button
    .tablenav__button--sort {
        padding-left: 15px;
        padding-right: 16px;
        .tablenav__sort {
            padding-right: 15px;
            font-size: 16px;
            border: none;
            background-color: transparent;
        }
    }
    // end sort button
    // delete button
    .tablenav__button--delete {
        color: white;
        background-color: $warm-grey;
        border: solid 1px #2a5272;
        padding-left: 20px;
        padding-right: 20px;
        &:hover {
            background-color: darken($warm-grey, 10%)
        }
        a {
            font-size: 16px;
            height: 32px;
            line-height: 32px;
        }
        i {
            font-size: 16px;
        }
    }
    // end delete button
    // pagination
    .tablenav__pagination {
        display: flex;
        align-items: center;
        margin-left: auto;
        .paginatiom__num {
            margin-right: 18px;
            color: $text-dark;
        }
        .pagination__btn {
            width: 34px;
            height: 34px;
            background-color: #fff;
            border: solid 1px #dfdfdf;
        }
        .pagination__btn.disabled {
            color: #dfdfdf;
        }
        .pagination__btn--next {
            margin-left: -2px;
        }
    }
    // end of the pagination
    // table
    .main__table {
        width: 100%;
        margin-top: 15px;
        margin-bottom: 20px;
        font-size: 16px;
        color: $text-dark;
        background-color: #fff;
        border: solid 2px #dfdfdf;
        .table__column--name {
            min-width: 8vw;
        }
        th,
        td {
            padding-left: 12px;
            padding-right: 12px;
            text-align: left;
            border: 1px solid #dfdfdf;
            box-sizing: border-box;
        }
        th {
            padding-top: 17px;
            padding-bottom: 17px;
            font-size: 14px;
            font-weight: bold;
            text-transform: uppercase;
        }
        td {
            padding-top: 12px;
            padding-bottom: 12px;
        }
        .table__check-cell {
            max-width: 51px;
            text-align: center;
            .input-box {
                text-align: center;
                padding-left: none;
                padding-right: none;
            }
        }
        .table__check-cell--active {
            -webkit-box-shadow: inset 2px 0px 0px 0px $ugly-blue;
            -moz-box-shadow: inset 2px 0px 0px 0px $ugly-blue;
            box-shadow: inset 2px 0px 0px 0px $ugly-blue;
        }
        .table__column--date {
            min-width: 9vw;
        }
    }
}

@media only screen and (max-width: 700px) {
    .main .main__tablenav .tablenav__actions>* {
        margin: 10px;
    }
    .main .btn--ugly-blue {
        margin-bottom: 10px;
    }
}

// FOOTER
.footer {
    padding: 21px 0 29px 0;
    background-color: $dark-teal;
    &__nav {
        display: flex;
        justify-content: center;
        align-items: center;
    }
    &__link {
        display: flex;
        align-items: center;
        min-height: 13px;
        padding-right: 25px;
        padding-left: 25px;
        border-right: 1px solid #fff;
        &:last-child {
            border-right: none;
        }
        a {
            font-size: 12px;
            color: #fff;
            &:hover {
                color: #e3e3e3;
                transition: color 0.05s linear;
            }
        }
    }
    &__copy {
        margin-top: 19px;
        text-align: center;
        color: #fff;
        font-size: 12px;
    }
}

// LEADS PAGE
.main {
    .main__table--leads {
        color: $ugly-blue;
        font-size: 14px;
        tr>td:nth-child(4) {
            text-align: center;
        }
        tr>td:nth-child(5) {
            text-align: center;
        }
        tr>td:nth-child(6) {
            text-align: center;
        }
        tr>td:nth-child(7) {
            text-align: center;
            font-weight: bold;
        }
        tr>td:last-child {
            padding-right: 4px;
        }
        .btn--comm .table__column--leadname {
            min-width: 14vw;
            max-width: 224px;
        }
        .table__column--lastvisit {
            min-width: 7.2vw;
            max-width: 115px;
        }
        .table__column--visits {
            min-width: 5.4vw;
            max-width: 86px;
            text-align: center;
        }
        .table__column--comm {
            width: 185px;
        }
        @media only screen and (max-width: 968px) {
            .table__column--comm {
                width: 60px;
            }
            .btn--comm {
                display: block;
                margin-bottom: 5px;
            }
        }
        .table__column--agent {
            min-width: 14vw;
            max-width: 224px;
        }
        // 1st column
        .table__check-cell {
            min-width: 3vw;
            max-width: 51px;
            text-align: center;
        }
        .table__check-cell--active {
            -webkit-box-shadow: inset 2px 0px 0px 0px rgb(207, 25, 31);
            -moz-box-shadow: inset 2px 0px 0px 0px rgb(207, 25, 31);
            box-shadow: inset 2px 0px 0px 0px rgb(207, 25, 31);
        }
        // 2nd column
        .table__username {
            font-size: 20px;
            font-weight: bold;
        }
        .table__userlocation {
            margin-top: 12px;
            font-size: 14px;
            text-align: left;
            color: #444444;
        }
        // 8th column
        .table__agent-action {
            margin-left: 13px;
            color: #444;
        }
        .agent-activity {
            i {
                color: #444;
            }
        }
        .btn--b {
            width: 33px;
            height: 28px;
            margin: 5px 5px 5px 8px;
            font-weight: bold;
            color: $ugly-blue;
            background-color: #ffffff;
            border: solid 1px #dfdfdf;
        }
        // custom select
        /* Reset Select */
        select {
            -webkit-appearance: none;
            -moz-appearance: none;
            -ms-appearance: none;
            appearance: none;
            outline: 0;
            box-shadow: none;
            border: 0 !important;
            background: #db1e27;
            background-image: none;
        }
        /* Custom Select */
        .table__select-hotness {
            display: inline-block;
            width: 82px;
            height: 28px;
            line-height: 28px;
        }
        .select {
            position: relative;
            display: block;
            margin: 0 auto;
            width: 100%;
            max-width: 65px;
            color: #fff;
            text-align: left;
            user-select: none;
            -webkit-touch-callout: none;
            .placeholder {
                position: relative;
                display: block;
                z-index: 1;
                height: 28px;
                line-height: 28px;
                padding-left: 15px;
                padding-right: 15px;
                border-radius: 3px;
                background-color: #aaa;
                font-weight: 700;
                cursor: pointer;
                &:after {
                    position: absolute;
                    right: -12px;
                    top: 50%;
                    transform: translateY(-50%);
                    color: #333333;
                    font-family: 'FontAwesome';
                    content: '\f0d7';
                    z-index: 10;
                }
            }
            &.is-open {
                .placeholder:after {
                    content: '\f0d8';
                }
                ul {
                    display: block;
                }
            }
            ul {
                display: none;
                position: absolute;
                width: 100%;
                background: #eee;
                border-radius: 3px;
                top: 100%;
                left: 0;
                list-style: none;
                margin: 5px 0 0 0;
                padding: 0;
                z-index: 100;
                border-radius: 3px;
                li {
                    display: block;
                    text-align: left;
                    padding-left: 18px;
                    color: #333;
                    font-weight: bold;
                    cursor: pointer;
                    &:hover {
                        background: #4ebbf0;
                        color: #fff;
                    }
                }
            }
        }
        // end custom select
        .btn--new {
            background-color: #dbc71e;
        }
        .btn--qual {
            background-color: #61db1e;
        }
        .btn--nurt {
            background-color: #1e84db;
        }
        .agent__name {
            margin-left: 10px;
        }
    }
}

// SHOWING REQUESTS PAGE
.main--requests {
    .main__table {
        font-size: 14px;
    }
    .tablenav__search-box {
        display: flex;
        align-items: center;
        .tablenav__search-field {
            font-size: 16px;
            height: 32px;
            padding-left: 14px;
            padding-right: 50px;
            color: #444444;
            background-color: #ffffff;
            border: solid 1px #dfdfdf;
        }
        .tablenav__search-button {
            width: 46px;
            height: 32px;
            margin-left: -2px;
            font-size: 16px;
            color: #fff;
            border: none;
            background-color: $ugly-blue;
        }
    }
    .table__username {
        font-size: 20px;
        font-weight: bold;
        color: $ugly-blue;
    }
    .table__status-cell {
        .table__status {
            display: inline-block;
            margin-right: 9px;
        }
        .btn--agentname {
            height: 28px;
            padding: 5px 20px;
            line-height: 0;
            color: $ugly-blue;
            background-color: #ffffff;
            border: solid 1px #dfdfdf;
        }
        @media only screen and (max-width: 968px) {
            .btn--agentname {
                display: block;
                margin: 5px 0 5px 0;
                padding: 5px 10px;
            }
            .btn {
                height: 15px;
                line-height: 15px;
            }
        }
        .btn--agentname--disabled {
            display: none;
        }
        .table__status-btn {
            float: right;
            position: relative;
            margin-right: 12px;
            cursor: pointer;
            i {
                font-size: 22px;
            }
            &:hover .table__status-popup {
                color: $ugly-blue;
            }
            &:hover .table__status-popup {
                display: block;
                &:after,
                &:before {
                    bottom: 100%;
                    right: 5px;
                    border: solid transparent;
                    content: " ";
                    height: 0;
                    width: 0;
                    position: absolute;
                    pointer-events: none;
                }
                &:after {
                    border-bottom-color: #fff;
                    border-width: 8px;
                    margin-left: -8px;
                }
                &:before {
                    border-bottom-color: rgba(2, 3, 3, 0.7);
                    border-width: 8px;
                    margin-left: -8px;
                }
            }
            .table__status-popup {
                display: none;
                position: absolute;
                right: -6px;
                padding: 7px 6px;
                z-index: 10;
                background-color: #ffffff;
                box-shadow: 0 0 7px 0 rgba(2, 3, 3, 0.23);
                border-radius: 3px;
            }
            .status__radio {
                display: block;
                width: 142px;
                height: 30px;
                line-height: 30px;
                border-bottom: 1px solid #dfdfdf;
                &:last-child {
                    border-bottom: none;
                }
                .table__status-name {
                    display: inline-block;
                    width: 100%;
                    padding-left: 17px;
                    font-size: 16px;
                    color: $text-dark;
                    box-sizing: border-box;
                }
                .table__status-name:hover {
                    color: #fff;
                    background-color: $ugly-blue;
                    cursor: pointer;
                }
            }
            &:after {
                content: "";
                display: table;
                clear: both;
            }
        }
    }
}

// BUILDINGS + BLOG PAGE
.main--bl {
    padding-top: 22px;
    .bl__search-box {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: flex-end;
        position: relative;
        margin-bottom: 5px;
        padding: 22px 15px 0px 15px;
        background-color: #ffffff;
        border: solid 2px #dfdfdf;
        &:after {
            content: '';
            position: absolute;
            bottom: -15px;
            height: 2px;
            width: 100%;
            background-color: #dfdfdf;
        }
        label {
            display: block;
            margin-bottom: 10px;
            color: $ugly-blue;
        }
        input,
        select {
            width: 100%;
            height: 32px;
